<template>
    <div class="installOrder-table">
        <el-table
            :data="tableData"
            :row-class-name="setRowClass"
            :stripe="true"
            size="small"
            style="width: 100%">
            <el-table-column align="center" width="80" prop="id" label="" sortable></el-table-column>
            <el-table-column align="center" prop="orderId" label="" sortable></el-table-column>
            <el-table-column align="center" prop="installDate" label="" sortable></el-table-column>
            <el-table-column align="center" prop="orderId" label="" sortable></el-table-column>
            <el-table-column align="center" prop="client" label="" sortable></el-table-column>
            <el-table-column align="center" prop="phone" label=""></el-table-column>
            <el-table-column align="center" width="220" prop="company" label="" sortable></el-table-column>
            <el-table-column align="center" prop="team" label="" sortable></el-table-column>
            <el-table-column align="center" prop="type" label="" sortable>
                <template slot-scope="scope">
                    <span
                        v-for="{name, type, color} in typeMap"
                        v-if="type===scope.row.type"
                        :style="{color: scope.row.status===3? '#9ca2ad':color}"
                    >{{name}}</span>
                </template>
            </el-table-column>
            <el-table-column align="center" prop="status" width="90" label="" sortable
                             class-name="sellOrder-table-noPadding">
                <template slot-scope="scope">
                    <div
                        v-for="{name, status, backgroundColor} in statusMap"
                        v-if="status===scope.row.status"
                        :style="{backgroundColor, color: '#FFF', height: '40px', lineHeight: '40px'}"
                    >{{name}}
                    </div>
                </template>
            </el-table-column>
            <el-table-column align="center" prop="createTime" label="" sortable></el-table-column>
            <el-table-column align="center" width="120" label="">
                <template slot-scope="scope">
                    <div style="text-align: left">
                        <installOrder-table-checkPop v-if="scope.row.status!==3">
                            <a style="margin-right: 5px"></a>
                        </installOrder-table-checkPop>
                        <installOrder-table-assignPop  v-if="scope.row.status===0">
                            <a style="margin-right: 5px"></a>
                        </installOrder-table-assignPop>
                        <common-confirm v-if="scope.row.status!==3&&scope.row.status!==2">
                            <span slot="content">AC-c1ab5f2d</span>
                            <a style="margin-right: 5px"></a>
                        </common-confirm>
                        <common-confirm v-if="scope.row.status===3">
                            <a style="margin-right: 5px"></a>
                        </common-confirm>
                    </div>
                </template>
            </el-table-column>
        </el-table>
    </div>
</template>

<script>
    import Vue from 'vue';
    import Common from 'components/Common';
    import AssignPop from './AssignPop';
    import CheckPop from './CheckPop';
    Vue.component('common-confirm', Common.Confirm);
    Vue.component('installOrder-table-assignPop', AssignPop);
    Vue.component('installOrder-table-checkPop', CheckPop);
    const statusMap = [{
        status: 0,
        name: '',
        backgroundColor: '#598cff',
    }, {
        status: 1,
        name: '',
        backgroundColor: '#f2be26',
    }, {
        status: 2,
        name: '',
        backgroundColor: '#5bc377',
    }, {
        status: 3,
        name: '',
        backgroundColor: '#aaabad',
    }];
    const typeMap = [{
        type: 0,
        name: '',
        color: '#326ae8',
    }, {
        type: 1,
        name: '',
        color: '#1c8ed3',
    }, {
        type: 2,
        name: '',
        color: '#df7b25',
    }, {
        type: 3,
        name: '',
        color: '#d9a70e',
    }];
    export default {
        props: ['tableData'],
        data() {
            return {
                statusMap,
                typeMap
            };
        },
        methods: {
            setRowClass({row, rowIndex}) {
                if (row.status === 3) {
                    return 'sellOrder-table-row-cancel';
                } else {
                    return '';
                }
            }
        }
    };
</script>

<style lang="less" scoped>
    @import 'index';
</style>



