<template>
    <div class="sellOrder-table">
        <el-table
            :data="tableData"
            :row-class-name="setRowClass"
            :stripe="true"
            size="small"
            style="width: 100%">
            <el-table-column width="100" prop="id" label=""></el-table-column>
            <el-table-column prop="orderId" label=""></el-table-column>
            <el-table-column prop="buyer" label=""></el-table-column>
            <el-table-column prop="payDate" label="" sortable></el-table-column>
            <el-table-column prop="appointDate" label="" sortable></el-table-column>
            <el-table-column width="100" prop="status" label="" sortable class-name="sellOrder-table-noPadding">
                <!--012345-->
                <template slot-scope="scope">
                    <div v-for="{status, name, backgroundColor} in statusMap"
                         v-if="scope.row.status===status"
                         :style="{backgroundColor, color: '#FFF', height: '40px', lineHeight: '40px'}"
                    >{{name}}
                    </div>
                </template>
            </el-table-column>
            <el-table-column label="" class-name="sellOrder-table-noPadding">
                <template slot-scope="scope">
                    <sellOrder-table-popover v-if="scope.row.status===0||scope.row.status===1" title="" :type="1">
                        <el-button class="addBtn"></el-button>
                    </sellOrder-table-popover>
                    <sellOrder-table-popover v-if="scope.row.status===2" title="" :type="2">
                        <a ></a>
                    </sellOrder-table-popover>
                    <common-confirm><a v-if="scope.row.status===3"></a></common-confirm>
                    <a v-if="scope.row.status===4||scope.row.status===5"></a>
                </template>
            </el-table-column>
        </el-table>
        <el-pagination
            style="float: right; margin-top: 15px"
            background
            layout="prev, pager, next"
            :current-page="1"
            :page-count="5"
            @current-change=""
        >
        </el-pagination>
    </div>
</template>

<script>
    import Vue from 'vue';
    import OrderPop from './OrderPop';
    import Common from 'components/Common';

    Vue.component('common-confirm', Common.Confirm);
    Vue.component('sellOrder-table-popover', OrderPop);
    const statusMap = [{
        status: 0,
        name: '',
        backgroundColor: '#598cff',
    }, {
        status: 1,
        name: '',
        backgroundColor: '#6bbef1',
    }, {
        status: 2,
        name: '',
        backgroundColor: '#5bc377',
    }, {
        status: 3,
        name: '',
        backgroundColor: '#aaabad',
    }, {
        status: 4,
        name: '',
        backgroundColor: '#fa6583',
    }, {
        status: 5,
        name: '',
        backgroundColor: '#fa6583',
    }];
    export default {
        props: ['tableData'],
        data() {
            return {
                statusMap: statusMap,
            };
        },
        methods: {
            setRowClass({row, rowIndex}) {
                if (row.status === 3) {
                    return 'sellOrder-table-row-cancel';
                } else {
                    return '';
                }
            }
        }
    };
</script>

<style lang="less" scoped>
    @import 'index';
</style>



