import Mock from 'mockjs';

const {Random, mock} = Mock;
const navList = [
    {num: '', name: '', status: 'all'},
    {num: '88', name: '', status: 'payment'},
    {num: '17', name: '', status: 'fahuo'},
    {num: '1', name: '', status: 'shipped'},
    {num: '1', name: '', status: 'tuihuan'},
    {num: '', name: '', status: 'completed'},
    {num: '', name: '', status: 'closed'}
];
// Mock

mock('http://userInfo.com', {
    userInfo: {
        user_name: '@cname',
        avatar: Random.image()
    },
});

mock('http://sellOrderList.com', {
    'list|15': [{
        'id|+1': 88,
        'orderId': () => Random.date('T'),
        'buyer': '@cname',
        'payDate': () => Random.datetime('yyyy-MM-dd HH:mm'),
        'appointDate': () => Random.datetime('yyyy-MM-dd HH:mm'),
        'status': () => Random.integer(0, 5), //012345
    }],
    navList,
});

mock('http://orderInfo.com', {
    'list|3': [{
        'product': {
            name: 'FTXX335RCNW / RXX335RCN 1.53X',
            attrs: [{
                attrs_name: '',
                attrs_types: ['FTXX335RCNW_1.5'],
            }]
        },
        'amount': () => Random.integer(2, 5),
        'distributed': () => Random.integer(1, 2),
    }],
});

mock('http://installOrderList.com', {
    'list|15': [{
        'id|+1': 88,
        'orderId': () => Random.date('T'),
        'installOrderId': () => Random.date('T'),
        'client': '@cname',
        'installDate': () => Random.datetime('yyyy-MM-dd'),
        'phone': () => Random.integer(13266700000, 13366700000),
        'company': 'XXXX',
        'team': 'A4',
        'type': () => Random.integer(0, 3),     //0123
        'createTime': () => Random.datetime('yyyy-MM-dd HH:mm'),
        'status': () => Random.integer(0, 3),   //0123
    }],
    navList,
});



